import Other from './Other.vue'
import MyClass from './MyClass.js'
import Functional from './Functional.vue'
export default {
  components: {
    Other,
    Functional
  },
  props: {
    msg: String,
    obj: null,
    ins: MyClass
  },
  data () {
    return {
      localMsg: this.msg,
      items: [1, 2],
      regex: /(a\w+b)/g,
      nan: NaN,
      infinity: Infinity,
      negativeInfinity: -Infinity,
      over: false
    }
  },
  computed: {
    awww () {
      return {
        a: {
          b: {
            c: 123
          }
        }
      }
    }
  },
  methods: {
    add () {
      const l = this.items.length
      this.items.push(
        l + 1,
        l + 2,
        l + 3
      )
    },
    rm () {
      this.items.pop()
    },
    inspect () {
      this.$inspect()
    }
  }
}
